import React from 'react';
import { graphql } from 'gatsby';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Chip from '@material-ui/core/Chip';
import { makeStyles } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Experience from '../components/experience';
import Layout from '../components/layout';
import SEO from '../components/seo';
import Education from '../components/education';
import '../global.css';
import { theme } from '../utils/theme';

const indexStyles = makeStyles({
  cardTitle: {
    color: `#fff`,
    fontSize: `1.38316rem`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  media: {
    height: `140`,
  },
  projectCardContainer: {
    display: `flex`,
    flexDirection: `column`,
  },
});

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const localStyles = indexStyles();
  // const posts = data.allMarkdownRemark.edges;

  return (
    <Layout
      location={location}
      title={siteTitle}
      style={{ display: `flex`, justifyContent: `center` }}
    >
      <SEO
        title="Andrew Stolzle"
        lang="en"
        description="All posts by Andrew Stolzle"
      />
      <Experience />
      <Education />
      <Card
        style={{
          backgroundColor: `#303030`,
          marginTop: `10px`,
        }}
      >
        <CardContent>
          <div className={localStyles.cardTitle}>
            <span role={'img'} aria-label={'Folder emoji'}>
              üìÇ
            </span>{' '}
            Recent Projects
          </div>
          <Card className={`project-card`}>
            <CardContent className={localStyles.projectCardContainer}>
              <div
                style={{
                  color: `#fff`,
                  fontSize: `1.38316rem`,
                  fontWeight: 800,
                  marginBottom: `10px`,
                }}
              >
                HPC Budget Manager
              </div>
              <small style={{ color: `#fff` }}>
                Budgeting suite for the financial office of Healing Place
                Church.
              </small>
              <ThemeProvider theme={theme}>
                <div style={{ display: `flex` }}>
                  <Chip
                    size="small"
                    label="Angular"
                    clickable
                    color="primary"
                  />
                  <Chip
                    size="small"
                    label="TypeScript"
                    clickable
                    color="secondary"
                  />
                  <Chip
                    size="small"
                    label="HTML/SASS"
                    clickable
                    color="primary"
                  />
                </div>
                {/* <Link to="/hpc/">
                  <Button variant="contained" color="primary">
                    Summary
                  </Button>
                </Link> */}
              </ThemeProvider>
            </CardContent>
          </Card>
        </CardContent>
      </Card>
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
